class BnavbarMenuController {
  /* @ngInject */
  constructor($rootScope,$timeout,$state) {
    this.rootScope = $rootScope;
    this.state = $state;
    this.directionsDisplay = null;
    this.rootScope.$on('ROUTE', (e,data)=>{
      $timeout(()=>{
        this.displayService().setDirections(data);
      });
    });
    this.rootScope.$on('LOGGEDIN', (e,data)=>{
      this.user = data;
    });
    this.rootScope.$on('LOGGEDOUT', (e,data)=>{
      this.user = null;
    });
    this.rootScope.$on('MAPRESET', (e,data)=>{

      $timeout(()=>{
        map = new google.maps.Map(document.getElementById('map'), {
          zoom: 7,
          center: data
        });
        this.displayService().setMap(map);
      });

    });

  }
displayService(){
  if(this.directionsDisplay==null){
    this.directionsDisplay = new google.maps.DirectionsRenderer;
    if(map){
      this.directionsDisplay.setMap(map);
    }
    this.directionsDisplay.setPanel(document.getElementById('right-panel'));
  }
  return this.directionsDisplay;
}
  routeMark(advice_lat, advice_lng, advice_text){
      this.removeMarker();
      var advice_pos = new L.LatLng(advice_lat, advice_lng);
      advice_marker = L.marker(advice_pos, {draggable: 'true', title: advice_text}).addTo(map);
      advice_marker.bindPopup(advice_text, {advice_text: true, autopan: true, zoomAnimation: true}).openPopup();
      advicemarkersList.push(advice_marker);
      map.fitBounds(advice_pos);
  }
   removeMarker() {
    for (var k = 0; k < advicemarkersList.length; k++) {
        map.removeLayer(advicemarkersList[k]);
    }
    advicemarkersList=new Array();
   }
   adviceDistance(index){
     let advices = this.context.route.steps;
     if(index+1!=advices.length){
       let distance =  advices[index+1].distance.value - advices[index].distance.value;
        return (distance >= 1000 ? (distance / 1000).toFixed(1) + " kilometers " : distance + " meters ");

     }
   }
}

export default BnavbarMenuController;



