class FeelController {
  /* @ngInject */
  constructor($state, $rootScope, $timeout, searchService, commonService) {
    this.done;
    this.context = {};
    this.rootScope = $rootScope;
    this.word = '';
    this.begin;
    this.wait = $timeout;
    this.isAppend = false;
    this.searchService = searchService;
    this.commonService = commonService;
    this.morsetoalpha = {
      "01": "a",
      "1000": "b",
      "1010": "c",
      "100": "d",
      "0": "e",
      "0010": "f",
      "110": "g",
      "0000": "h",
      "00": "i",
      "0111": "j",
      "101": "k",
      "0100": "l",
      "11": "m",
      "10": "n",
      "111": "o",
      "0110": "p",
      "1101": "q",
      "010": "r",
      "000": "s",
      "1": "t",
      "001": "u",
      "0001": "v",
      "011": "w",
      "1001": "x",
      "1011": "y",
      "1100": "z",
      "01111": "1",
      "00111": "2",
      "00011": "3",
      "00001": "4",
      "00000": "5",
      "10000": "6",
      "11000": "7",
      "11100": "8",
      "11110": "9",
      "11111": "0"
    }
    navigator.vibrate = navigator.vibrate ||
      navigator.webkitVibrate ||
      navigator.mozVibrate ||
      navigator.msVibrate;


    if (navigator.vibrate) {
      console.log('we can vibrate');
    } else {
      console.log('no vibration for you :-(');
    }

    this.alphatomorse = {};
    for (var mcode in this.morsetoalpha) {
      this.alphatomorse[this.morsetoalpha[mcode]] = mcode;
    }
    if ("ontouchstart" in window) {
      $('#taparea').bind('touchstart', (e) => this.start(e));
      $('#taparea').bind('touchend', (e) => this.end(e));
    } else {
      $('#taparea').bind('mousedown', (e) => this.start(e));
      $('#taparea').bind('mouseup', (e) => this.end(e));
    }
    this.commonService.setupNavigator(this);
  }
  next() {
    let currData = this.context[this.context.linker];
    if (currData.data.length - 1 == currData.index) {
      this.utter('End')
    } else {
      currData.index++;
      this.discuss(this.context.linker, currData.data, currData.index)

    }
  }
  yes(e) {
    console.log('In drag yes', e);
    e.stopPropagation();
    if (this.word) {
      let msg = this.fromMorse(this.word);
      this.commonService.search(msg, this, {
        end: ' end',
        deviate: ' deviation',
        careful: ' careful',
        noroute: ' 0 route',
        localSearch: "f ",
        localShop: "s ",
        localEat: "e ",
        localShare:"h",
        location:"l",
        carpool:"r",
        book:"b",
        navigate: "n",
        found:' results',
        notfound:'0 result',
        bookok:'OK',
        shareok:'OK',
        ridesearchok:'OK',
        locatefirst:'locate',
        loginfirst:'login'
      });
      this.word = '';
    }else if(this.context.linker) {
      this.next();
    }

  }
  discuss(type, object, index) {
    switch (type) {
      case 'localSearch':
        this.utter(`${object[index].name}.`);
        break;
      case 'localEat':
        this.utter(`${object[index].name}`);
        break;
      case 'localShop':
        this.utter(`${object[index].product_name}, ${object[index].store_name}`);
        break;
    }
  }
  no(e) {
    //console.log('no',e);
    e.stopPropagation();
    this.word = '';
    this.utter("cancel");
    this.context = {};
    if (window.existingWatch) {
      navigator.geolocation.clearWatch(window.existingWatch);
      window.existingWatch = null;
    }
  }
  start(e) {
    console.log("In start");
    e.preventDefault();

    this.begin = new Date();

    if (this.done && (this.begin - this.done < 500)) {
      this.isAppend = true;
    } else if (this.done && (this.begin - this.done > 1200)) {
      this.isAppend = 'wrdspc';
    } else {
      this.isAppend = false;
    }

  }
  end(e) {
    console.log("In end");
    e.preventDefault();
    this.done = new Date();

    let duration = this.done - this.begin;

    var c;
    if (duration < 250) {
      c = 0;
    } else { // if(duration>300 && duration<=1000){
      c = 1;
    }

    if (this.isAppend === true) {
      this.word += c;
    } else if (this.isAppend == 'wrdspc') {
      this.word += 'X' + c;
    } else {
      this.word += '-' + c;
    }
    $("#textarea").html(this.fromMorse(this.word));
  }

  fromMorse(sentence) {
    sentence = sentence.substr(1);
    let words = sentence.split("X");
    let readable = "";
    for (let i in words) {
      let word = words[i];
      let letters = word.split("-");
      for (let y in letters) {
        let letter = letters[y];
        //console.log(letter,letters);
        if (this.morsetoalpha[letter]) {
          readable += this.morsetoalpha[letter];
        }
      }
      readable += " ";
    }
    return readable;
  }

  toMorse(sentence) {
    sentence = sentence.toLowerCase();
    let words = sentence.split(" ");
    let morse = "";
    for (let i in words) {
      let word = words[i];
      for (let y in word) {
        let letter = word[y];
        morse += "-" + this.alphatomorse[letter];
      }
      morse += "X";
    }
    return morse;
  }
  utter(text) {
    text = text.replace(/<b>/g, '').replace(/<\/b>/g, '').replace(/<em>/, '').replace(/<\/em>/g, '');
    console.log(text);
    let word = this.toMorse(text);
    var l = word.length;
    var message = [];
    var i = 0;
    var s = 1;
    var lastExists = false;
    while (++i < l) {
      if (word[i] == 0) {
        if ((word[i - 1] == "-") || (word[i - 1] == "X")) {
          message.push(200);
        } else {
          message.push(70);
          message.push(200);
        }
      } else if (word[i] == 1) {
        if ((word[i - 1] == "-") || (word[i - 1] == "X")) {
          message.push(600);
        } else {
          message.push(70);
          message.push(600);
        }
      } else if (word[i] == "-") {
        message.push(600);
      } else if (word[i] == "X") {
        message.push(1400);
      }
    }
    navigator.vibrate(message);
    $("#textarea").html(text);
  }
  processRoute(route) {
    this.context.route = route;
    let length = (route.distance.value) / 1000;
    this.utter(`${length.toFixed(1)} km`);
    this.utter(`${route.duration.text}`);

  }
}

export default FeelController;



