import angular from 'angular';
import uiRouter from 'angular-ui-router';
import feelService from './feel.service';
import feelComponent from './feel.component';

let feelModule = angular.module('feel', [
  uiRouter
])
.config(($stateProvider) => {
  "ngInject";
  $stateProvider
    .state('FEEL', {
      url: '/feel',
      template: '<feel></feel>'
    });
})

.service('feelService', feelService)
.component('feel', feelComponent);

export default feelModule;



