export default class SearchService {

  /* @ngInject */
  constructor($http, $q, config) {
    this.http = $http;
    this.googleServiceRef = null;
    this.q = $q;
    this.config = config;
    this.crimeLocs = [{
        lat: 30.731938000000099,
        lng: 76.765338999999997,
        name: "3128/2 sec 22d chandigarh"
      },
      {
        lat: 30.736111000000101,
        lng: 76.769575000000003,
        name: "hno 223 sec 22a chandigarh"
      },
      {
        lat: 30.735039,
        lng: 76.777608000000001,
        name: "sco 1030/31 sector 22b chandigarh"
      },
      {
        lat: 30.73958,
        lng: 76.762414000000007,
        name: "hno 3276 Sec 23d chandigarh"
      },
      //below if a dummy crime location to test only
      {
        lat: 30.699671900000002,
        lng: 76.7216153,
        name: "hig 999 Sec 70 mohali"
      }
    ];
  }
  calcCrow(lat1, lon1, lat2, lon2) {
    var R = 6371; // km
    var dLat = this.toRad(lat2 - lat1);
    var dLon = this.toRad(lon2 - lon1);
    var lat1 = this.toRad(lat1);
    var lat2 = this.toRad(lat2);

    var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.sin(dLon / 2) * Math.sin(dLon / 2) * Math.cos(lat1) * Math.cos(lat2);
    var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    var d = R * c;
    return d;
  }

  toRad(Value) {
    return Value * Math.PI / 180;
  }
  //POC to make user aware of crime scenes and be careful.
  isNearCrimeLoc(coords) {
    let defer = this.q.defer();
    this.http.get(`https://data.police.uk/api/crimes-street/all-crime?lat=${coords.latitude}&lng=${coords.longitude}`)
      .success(data => {
        let nearby = {};
        for (var i = 0; i < data.length; i++) {
          let loc = data[i];
          let distance = this.calcCrow(coords.latitude, coords.longitude, loc.location.latitude, loc.location.longitude) * 1000;
          if (distance <= 30) {
            nearby.name= loc.location.name;
            break;
          }
        }
        return false;
        defer.resolve(nearby)
      })
      .error(msg => defer.reject(msg));

    return defer.promise;


  }

  localSearch(keyword) {

    let defer = this.q.defer();
    this.http.get(`${this.config.apiHost}search/local-search?city=London&q=${keyword}`)
      .success(data => defer.resolve(data.results))
      .error(msg => defer.reject(msg));

    return defer.promise;
  }
  postRide(ride,user) {

    let defer = this.q.defer();
    this.http.post(`${this.config.ridesHost}api/ride`,ride,{
        headers: { 'X-AUTH-SMARTCITY': `${user.session}`}})
      .success(data => defer.resolve(data))
      .error(msg => defer.reject(msg));

    return defer.promise;
  }
  searchRide(ride,user) {

    let defer = this.q.defer();
    this.http.post(`${this.config.ridesHost}api/ride/search`,ride,{
        headers: { 'X-AUTH-SMARTCITY': `${user.session}`}})
      .success(data => defer.resolve(data))
      .error(msg => defer.reject(msg));

    return defer.promise;
  }
  bookRide(rideid,user) {

    let defer = this.q.defer();
    this.http.post(`${this.config.ridesHost}api/ride/${rideid}/contact`,{},{
        headers: { 'X-AUTH-SMARTCITY': `${user.session}`}})
      .success(data => defer.resolve(data))
      .error(msg => defer.reject(msg));

    return defer.promise;
  }
  localShop(keyword) {

    let defer = this.q.defer();
    this.http.get(`${this.config.apiHost}search/shop?q=${keyword}`)
      .success(data => defer.resolve(data.products))
      .error(msg => defer.reject(msg));

    return defer.promise;
  }
  localEat(keyword) {

    let defer = this.q.defer();
    this.http.get(`${this.config.apiHost}search/restaurants?city=London&q=${keyword}`)
      .success(data => defer.resolve(data.data))
      .error(msg => defer.reject(msg));

    return defer.promise;
  }
  route(start, end) {
    let defer = this.q.defer();
    //test cordinates for London
    start = {lat:51.5124098090718, lng:-0.1268785006157669};
    this.directionService().route({
          origin: start,
          destination: end,
          travelMode: 'DRIVING'
        }, (response,status)=>{
          if (status === 'OK') {
            defer.resolve(response);
            //directionsDisplay.setDirections(response);
          } else {
            defer.reject(status)
          }
        });

    return defer.promise;
  }
  directionService(){
    if(this.googleServiceRef==null){
      this.googleServiceRef = new google.maps.DirectionsService;
    }
    return this.googleServiceRef;
  }
  deals(lat, lng, keyword) {
    let defer = this.q.defer();
    this.http.get(this.config.apiHost + `nearby_search?lat=${lat}&lng=${lng}&keywords=${keyword}&page=1`)
      .success(data => defer.resolve(data))
      .error(msg => defer.reject(msg));

    return defer.promise;
  }
  validate(question) {

    let defer = this.q.defer();

    this.http.post(this.config.apiHost + 'questions', question)
      .success(data => defer.resolve(data))
      .error(msg => defer.reject(msg));

    return defer.promise;
  }
}



