class StartController {
  /* @ngInject */
  constructor($state, $rootScope, $timeout, searchService, commonService) {
    this.searchService = searchService;
    this.rootScope = $rootScope;
    this.state = $state;
    this.context = {};
    this.commonService = commonService;
    this.wait = $timeout;

    let supported = this.commonService.setupNavigator(this);
    if (!supported) {
      utter("Navigation is not supported by this app");
    }
  }





  next() {
    let currData = this.context[this.context.linker];
    if (currData.data.length - 1 == currData.index) {
      this.utter('That was the end of the result list.')
    } else {
      currData.index++;
      this.discuss(this.context.linker, currData.data, currData.index)

    }
  }
  onSpeechResult(msg, _this) {
    if (msg != 'no-speech') {
      Materialize.toast(msg, 1000);
    }
    console.log(msg);
    //shop
    //search
    //eat
    //navigate
    //next
    this.commonService.search(msg, this, {
       end:' Ending route.',
       deviate:' Possible deviation from route.',
       noroute:'No direct route found',
      localSearch: "search",
      localShop: "shop",
      localShare:"share",
      location:"location",
      carpool:"ride sharing",
      book:"book",
      localEat: "eat",
      navigate: "navigate",
      next:"next",
      yes:"yes",
      no:"no",
      found:' results found. Say NEXT to start browsing the list.',
      notfound:'Nothing found. Please broaden your search',
      bookok:'Ride booked successfully',
      shareok:'Ride shared successfully'
    });
    window.closeSpeech();
  }
  search() {
    var _this = this;
    isStopped = false;
    if (!window.recognition) {
      if (isCordova) {
        console.log('register for iscordova');
        //recognitizer 1
        // window.recognition = new SpeechRecognition();
        //recognizer 2
        window.recognition = window.plugins.nodialogrecognizer.startRecognize;
      } else {
        console.log('register for non cordova');
        window.recognition = new webkitSpeechRecognition();
      }
      window.recognition.lang = "en-IN";
      window.recognition.continuous = false;
      window.recognition.onresult = (event) => {
        var length = event.results.length;
        var msg = event.results[length - 1][0].transcript;
        this.onSpeechResult(msg, this);
      }
      recognition.onerror = function(event) {
        if (event && event.error == 'network') {
          alert("Please check your internet connection!");
        }
        console.log("Error event", event);
      }
      recognition.onend = function() {
        window.closeSpeech();
      }
    }
    this.utter("Yes please ?", () => this.startRecognition());
  }
  startRecognition() {
    //window.recognition.start();
    if (isCordova) {
      window.recognition(data => {
        this.onSpeechResult(data[0], this);
      }, (code) => {
        console.log("error", code);
        this.utter("Didn't get you!")
        window.closeSpeech();
      }, 1);
    } else {
      //for web
      window.recognition.onresult = (event) => {
        let msg = event.results[0][0].transcript;
        console.log(msg);
        this.onSpeechResult(msg, this);
      }
      window.recognition.start();
    }
  }
  utter(text, callback) {
    text = text.replace(/<b>/g, '').replace(/<\/b>/g, '').replace(/<em>/, '').replace(/<\/em>/g, '');
    var uttr = new SpeechSynthesisUtterance();
    uttr.lang = "en-IN";
    uttr.text = text;
    uttr.onend = callback;

    window.speechSynthesis.speak(uttr);
  }
  discuss(type, object, index) {
    switch (type) {
      case 'localSearch':
        this.utter(`${object[index].name}. ${object[index].snippet}`);
        break;
      case 'localEat':
        this.utter(`${object[index].name} at ${object[index].address}`);
        break;
      case 'localShop':
        this.utter(`${object[index].product_name} available at ${object[index].store_name}`);
        break;
    }
  }
  processRoute(route) {
    this.context.route = route;
    let length = (route.distance.value) / 1000;
    this.utter(`Your destination is ${length.toFixed(1)} kilometers away`);
    this.utter(`Your should reach there in ${route.duration.text}`);

  }

}

export default StartController;



