class LoginController {
  /* @ngInject */
  constructor(commonService,$state,$rootScope,config,$window,$timeout) {
    this.userService = commonService;
    this.rootScope = $rootScope;
    this.state = $state;
    this.loginHost = config.loginHost;
      $window.onmessage = (e)=>{

        let user = JSON.parse(e.data);
        console.log(user);
        this.userService.setUser(user);
        this.rootScope.$broadcast('LOGGEDIN', user);
        this.state.go("START");
      };



  }
  logout(){

  }
  start(){
    //Add promo cash to every new user
    if(!this.user.get("isOld")){
      this.user.set("wallet",JSON.stringify({cash:5,event:'NEW_USER'}));
      this.user.set("isOld",true);
      alert("Congrats! You have received Rs 5. Use this cash to win more cash!");
    }
    if(!this.user.get("tut")){
      alert("Lets start with a simple tutorial.");
      this.state.go("TUTORIAL");
    }


  }
}

export default LoginController;



