class SettingsController {
  /* @ngInject */
  constructor() {
    this.speak = true;
    this.feel = false;
    this.type=false;
    this.simulate=true
  }
  setMode(type){
    this.speak = false;
    this.feel = false;
    this.type=false;

    this[type] = true;

  }
}

export default SettingsController;



