export default
class commonService {
  /*@ngInject*/
  constructor($http, $q, config, $state, $timeout, $rootScope, searchService) {
    this.http = $http;
    this.q = $q;
    this.config = config;
    this.state = $state;
    this.timeout = $timeout;
    this.searchService = searchService;
    this.user = null;
    this.rootScope = $rootScope;
  }
  setupNavigator(_this) {
    $('#modal1').openModal({
      dismissible: true
    });

    if (navigator.geolocation) {
      if (window.existingWatch) {
        navigator.geolocation.clearWatch(window.existingWatch);
        window.existingWatch = null;
      }

      navigator.geolocation.getCurrentPosition(data => {
        _this.currentLoc = {
          lat: data.coords.latitude,
          lng: data.coords.longitude
        };
        this.timeout(() => {
          this.rootScope.$broadcast('MAPRESET', _this.currentLoc);
        });
        $('#modal1').closeModal();

      });
      return true;
    } else {
      $('#modal1').closeModal();
      return false;
    }
  }
  initiateNavigator(_this, messages) {
    let linker = _this.context.linker;
    if (linker == 'localSearch' || linker == 'localEat') {
      let dataSet = _this.context[linker];
      let currSearch = dataSet.data[dataSet.index];
      if (currSearch && (currSearch.latitude || (currSearch.coordinates && currSearch.coordinates.latitude))) {
        let destination = {
          lat: linker == 'localEat' ? parseFloat(currSearch.latitude) : currSearch.coordinates.latitude,
          lng: linker == 'localEat' ? parseFloat(currSearch.longitude) : currSearch.coordinates.longitude
        };
        _this.searchService.route(_this.currentLoc, destination)
          .then(data => {
            //set route data
            console.log(data);
            _this.rootScope.$broadcast('ROUTE', data);
            let way = data.routes[0].legs[0];
            _this.processRoute(way);


          }, () => _this.utter(messages.noroute));
      }

    } else {

    }
  }
  startNavigation(_this, messages) {
    if (_this.context.route) {
      if (window.existingWatch) {
        navigator.geolocation.clearWatch(window.existingWatch);
        window.existingWatch = null;
      }

      navigator.geolocation.getCurrentPosition(data => {
        _this.currentLoc = {
          lat: data.coords.latitude,
          lng: data.coords.longitude
        };
        _this.context.watchid = navigator.geolocation.watchPosition(
          (data) => _this.commonService.detectAdvice(data, _this,
            messages),
          _this.commonService.watcherror, {
            timeout: 30000,
            enableHighAccuracy: true
          });
        window.existingWatch = _this.context.watchid;
      });
    }
  }
  search(msg, _this, messages) {
    msg = msg.trim();
    if (window.existingWatch) {
      navigator.geolocation.clearWatch(window.existingWatch);
      window.existingWatch = null;
    }
    if (msg.indexOf(messages.localSearch) != -1) {
      let keyword = msg.split(messages.localSearch)[1];
      this.processSearchResults('localSearch', keyword, _this, messages);
    } else if (msg.indexOf(messages.localShop) != -1) {
      let keyword = msg.split(messages.localShop)[1];
      this.processSearchResults('localShop', keyword, _this, messages);
    } else if (msg.indexOf(messages.localEat) != -1) {
      let keyword = msg.split(messages.localEat)[1];
      this.processSearchResults('localEat', keyword, _this, messages);
    } else if (msg.indexOf(messages.localShare) != -1) {
      let keyword = msg.split(messages.localShare)[1];
      this.shareRide(_this, messages);
    } else if (msg.indexOf(messages.carpool) != -1) {
      this.searchRide(_this, messages);
    } else if (msg.indexOf(messages.book) != -1) {
      this.bookRide(_this, messages);
    }else if (msg.indexOf(messages.location) != -1) {
      _this.commonService.initiateNavigator(_this, messages);
    } else if (msg.indexOf(messages.navigate) != -1) {
      _this.commonService.initiateNavigator(_this, messages);
      _this.commonService.startNavigation(_this, messages);
    } else if (msg.indexOf(messages.next) != -1) {
      _this.next();

    } else if (msg.indexOf(messages.yes) != -1) {
      _this.next();

    } else if (msg.indexOf(messages.no) != -1) {

    }
  }
  formatToday(hr) {
    let today = new Date();
    let dd = today.getDate();
    let mm = today.getMonth() + 1; //January is 0!

    let yyyy = today.getFullYear();
    if (dd < 10) {
      dd = '0' + dd;
    }
    if (mm < 10) {
      mm = '0' + mm;
    }
    return `${yyyy}-${mm}-${dd}T${hr}:00:0`;
  }
  bookRide(_this,messages){
    if(!_this.context.route){
      _this.utter(messages.locatefirst || "Please first locate your result by saying location.");
      return;
    }
    if (this.user && _this.context.ride) {
      this.searchService.bookRide(_this.context.ride.id, this.user)
        .then(data => {
          _this.utter(messages.bookok)
        });
    }else{
      _this.utter(messages.loginfirst || "Please login before you share your ride.");
    }
  }
  shareRide(_this, messages) {
    if(!_this.context.route){
      _this.utter(messages.locatefirst || "Please first locate your result by saying location.");
      return;
    }


    if (this.user && _this.context.route) {
      let start = _this.context.route.start_location;
      let end = _this.context.route.end_location;
      let ride = {
        "ride": {
          "rglr": false,
          "prc": "20",
          "vhcl": {
            "typ": "CAR",
            "num": "X",
            "nam": "Standard Sedan"
          },
          "days": [this.formatToday(10)],
          "tm": {
            "hr": "10",
            "full": "10:00 AM"
          },
          "src": {
            "nam":_this.context.route.start_address,
            "loc": {
              "coordinates": [start.lng(), start.lat()]
            }
          },
          "dst": {
            "nam":_this.context.route.end_address,
            "loc": {
              "coordinates": [end.lng(), end.lat()]
            }
          }
        },
        "strtTm": "10:00 AM"
      };
      this.searchService.postRide(ride, this.user)
        .then(data => {
          _this.utter(messages.shareok)
        });
    } else {
      _this.utter(messages.loginfirst || "Please login before you share your ride.");
    }
  }

  searchRide(_this, messages) {
    if(!_this.context.route){
      _this.utter(messages.locatefirst || "Please first locate your result by saying location.");
      return;
    }

    if (this.user && _this.context.route) {
      let start = _this.context.route.start_location;
      let end = _this.context.route.end_location;
      let ride = {

        "days": [this.formatToday(0),this.formatToday(23)],
        "src": {
          "loc": {
            "coordinates": [start.lng(), start.lat()]
          }
        },
        "dst": {
          "loc": {
            "coordinates": [end.lng(), end.lat()]
          }
        }

      };

      this.searchService.searchRide(ride, this.user)
        .then(data => {
          if (data && data.results.length > 0) {
            let ride = data.results[0];
            let uname = ride.by.name;
            let cost = ride.prc;
            let src = ride.src.nam;
            let dst = ride.dst.nam;
            let time = ride.tm.full;
            _this.context.ride = ride;
            let message  = messages.ridesearchok? `Price ${cost}`:`${uname} is offering a ride from ${src} to ${dst} at a price of ${cost}.
              The ride starts around ${time}`;
            _this.utter(message);
          } else {
            _this.utter(`No carpool found`);
          }


        });
    } else {
      _this.utter(messages.loginfirst||"Please login to perform this action.");
    }
  }
  processSearchResults(type, keyword, _this, messages) {
    _this.context = {};
    this.searchService[type](keyword)
      .then(data => {
        console.log(data);
        if (data && data.length > 0) {
          _this.context[type] = {
            data: data,
            index: -1
          };
          _this.utter(`${data.length} ${messages.found}`);
          _this.context.linker = type;
        } else {
          _this.utter(`${messages.notfound}`);
        }
      });
    Materialize.toast('Searching ... ', 1000);
  }
  detectAdvice(data, _this, messages) {
    //process location every 3 seconds
    if (_this.context.lastLocTime == undefined || _this.context.lastLocTime < (new Date().getTime() - 5000)) {
      _this.context.lastLocTime = new Date().getTime();
    } else {
      return;
    }

    Materialize.toast("Navigating ...", 1000);

    //alert("Change event received");
    //$("#map1").html($("#map1").html() + data.coords.accuracy +" | "+data.coords.latitude+" | "+ data.coords.longitude);
    let advices = _this.context.route.steps;
    let index = _this.context.currentAdvice || 0;
    let uindex = _this.context.uadvice || 0;
    let speech = '';
    if (index == 0 && uindex == 0) {
      let distance = advices[index + 1].distance.value - advices[index].distance.value;
      speech = advices[index].instructions + ' And go ' + (distance >= 1000 ? (distance / 1000).toFixed(1) + " kilometers " : distance + " meters ");
      uindex++;

    } else {
      console.log("start location ", advices[uindex].start_location.lng());
      let distance = _this.searchService.calcCrow(data.coords.latitude, data.coords.longitude, advices[uindex].start_location.lat(), advices[uindex].start_location.lng()) * 1000;
      console.log(distance, ' meters');
      let upcomingD = advices[uindex].distance.value - advices[index].distance.value;

      if (distance <= 20) {
        //speak in distance uindex and uindex++ and index = uindex
        speech = 'In ' + distance.toFixed(1) + ' meters, ' + advices[uindex].instructions;

        if (uindex != (advices.length - 1)) {
          speech += ' And go ' + (advices[uindex + 1].distance.value - advices[uindex].distance.value).toFixed(1) + ' meters.';
          uindex++;
          index++;
        } else {
          navigator.geolocation.clearWatch(_this.context.watchid);
          _this.context = {};
          speech += messages.end;
        }

      } else if (distance > (upcomingD + 10)) {
        console.log("Deviation upcomingD", upcomingD, "| Crow distance init", data.coords, advices[uindex]);
        speech += messages.deviate;
      } else {
        if (_this.context.lastSpeakTime == undefined || _this.context.lastSpeakTime < (new Date().getTime() - 20000)) {
          _this.context.lastSpeakTime = new Date().getTime();
          speech += 'In ' + distance.toFixed(1) + ' meters, ' + advices[uindex].instructions;
        }
      }
    }
    _this.context.uadvice = uindex;
    _this.context.currentAdvice = index;

    //check near any crime location
    _this.searchService.isNearCrimeLoc(data.coords).then(data => {
      if (data.name) {
        _this.utter(messages.careful || `Be careful. Suspected crime location ${data.name}`);
      }
    });

    if (speech) {
      _this.utter(speech);
    }

  }
  getUser() {
    return this.user;
  }
  setUser(user) {
    this.user = user;
  }
  watchError() {
    Materialize.toast("Watch error", 1000);
  }
}



