import $ from './common/jqueryModule';
import 'materialize-css';
import angular from 'angular';
import uiRouter from 'angular-ui-router';
import touch from 'angular-touch';
import Components from './components/components';
import AppComponent from './app.component';
import properties from './common/properties';
import CommonService from './common/commonService';

angular.module('app', [
    uiRouter,touch,
    // Common.name,
    Components.name
])

    .config(($locationProvider, $httpProvider, $compileProvider,$sceProvider) => {
        "ngInject";
        // pass cross origin cookie
       // $httpProvider.defaults.withCredentials = true;
        $compileProvider.aHrefSanitizationWhitelist(/^\s*(https?|file|blob|ftp|mailto|c‌​hrome-extension|data):/);
        $sceProvider.enabled(false);
    })

    .constant('config', properties)
    .component('app', AppComponent)
    .service('commonService', CommonService);

//bootstrap the app
angular.element(document).ready(function () {
  if (window.cordova) {
    console.log("Running in Cordova, will bootstrap AngularJS once 'deviceready' event fires.");
    document.addEventListener('deviceready', function () {
      console.log("Deviceready event has fired, bootstrapping AngularJS.");
      angular.bootstrap(document.body, ['app']);
      /*console.log("Ad enabled",AdMob);
      if (AdMob) {
        AdMob.createBanner({
            adId : 'ca-app-pub-8173580720444624/3962546195',
            position : AdMob.AD_POSITION.BOTTOM_CENTER,
            autoShow : true
        });
    }*/
    }, false);
  } else {
    console.log("Running in browser, bootstrapping AngularJS now.");
    angular.bootstrap(document.body, ['app']);
  }
});



